
import { defineComponent, ref, watch } from "vue"

export default defineComponent({
  emits: ["handleSubmit"],
  props: {
    modelValue: {
      type: Object,
      default: () => null
    },
    contentFormConfig: {
      type: Object,
      require: true
    }
  },
  setup(props, { emit }) {
    let formData: any = null
    watch(
      () => props.modelValue,
      () => {
        if (!props.modelValue) {
          const formItems = props.contentFormConfig?.propList ?? []
          const formOriginData: any = {}

          for (const item of formItems) {
            formOriginData[item.name] = ""
          }
          formData = ref(formOriginData)
        } else {
          formData = ref({ ...props.modelValue })
        }
      },
      {
        deep: true,
        immediate: true
      }
    )

    const showPicker = ref(false)

    const handleSubmit = () => {
      emit("handleSubmit", formData.value)
    }
    const handleSelectConfirm = (value: any, name: any) => {
      formData.value[name] = value
      console.log(value, name, formData)
      showPicker.value = false
    }
    return {
      formData,
      handleSubmit,
      handleSelectConfirm,
      showPicker
    }
  }
})
